.courses-container {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    width: 100%;
}

table {
    border-collapse: collapse;
    margin: 20px 0;
}

table,
th,
td {
    border: 1px solid black;
}

th,
td {
    padding: 8px 12px;
}

th {
    background-color: rgb(40, 151, 170);
    text-align: left;
}

tr:hover {
    background-color: #f5f5f5;
}

h1 {
    color: rgba(13, 13, 15, 0.965);
}
h2 {
    color: #b26123;
}

.prefixColor {
    background-color: #239fb2;
}
.invisableText {
    color: #2c1111;
}

.Tablesize {
    width: 38%;
}
.tableBetween {
    padding-bottom: 40px;
}
.AdjustTitle {
    font-size: 30px;
    color: #971038;
}
.adjustrow {
    width: 18%;
}
.modifyCourseList {
    text-align: left;
    color: #000000;
    font-size: 28px;
}
.modifyErrorTitle {
    color: rgb(255, 0, 76);
}
